'use client';

import { Button } from '@/components/ui/button';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { zodResolver } from '@hookform/resolvers/zod';
import {
  Document,
  Page,
  pdf,
  PDFViewer,
  StyleSheet,
  Text,
  View,
} from '@react-pdf/renderer';
import { saveAs } from 'file-saver';
import { DownloadIcon, EyeIcon, XIcon } from 'lucide-react';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { z } from 'zod';
import { User } from '../types/user';

const formSchema = z.object({
  name: z
    .string()
    .min(3, 'Nama minimal 3 karakter')
    .max(50, 'Nama maksimal 50 karakter'),
  email: z.string().email('Email tidak valid').min(5, 'Email terlalu pendek'),
  phone: z.string().optional(),
  companyName: z
    .string()
    .max(100, 'Nama perusahaan maksimal 100 karakter')
    .optional(),
  street: z.string().max(100).optional(),
  city: z.string().max(50).optional(),
  zipcode: z.string().max(20).optional(),
});

type FormValues = z.infer<typeof formSchema>;

const styles = StyleSheet.create({
  page: { padding: 40, fontFamily: 'Helvetica', backgroundColor: '#ffffff' },
  header: {
    fontSize: 24,
    marginBottom: 20,
    textAlign: 'center',
    color: '#1e40af',
    fontWeight: 'bold',
  },
  text: { fontSize: 14, marginBottom: 12, color: '#374151', lineHeight: 1.5 },
  branding: {
    fontSize: 10,
    position: 'absolute',
    bottom: 20,
    left: 40,
    color: '#6b7280',
    fontStyle: 'italic',
  },
  watermark: {
    fontSize: 40,
    color: 'rgba(59, 130, 246, 0.1)',
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'rotate(-45deg)',
    textAlign: 'center',
  },
});

const UserProfilePDF = ({
  user,
  template = 'simple',
}: {
  user: FormValues;
  template?: 'simple' | 'detailed';
}) => (
  <Document>
    <Page size='A4' style={styles.page}>
      {template === 'simple' ? (
        <View>
          <Text style={styles.header}>Profil Pengguna</Text>
          <View
            style={{ borderBottom: '1px solid #e5e7eb', marginBottom: 15 }}
          />
          <Text style={styles.text}>Nama: {user.name}</Text>
          <Text style={styles.text}>Email: {user.email}</Text>
          <Text style={styles.text}>Telepon: {user.phone}</Text>
          {user.street && <Text style={styles.text}>Jalan: {user.street}</Text>}
          {user.city && <Text style={styles.text}>Kota: {user.city}</Text>}
          {user.zipcode && (
            <Text style={styles.text}>Kode Pos: {user.zipcode}</Text>
          )}
          {user.companyName && (
            <Text style={styles.text}>Perusahaan: {user.companyName}</Text>
          )}
        </View>
      ) : (
        <View>
          <Text style={styles.header}>Detail Profil Pengguna</Text>
          <View
            style={{ borderBottom: '2px solid #3b82f6', marginBottom: 20 }}
          />
          <Text style={styles.text}>Nama Lengkap: {user.name}</Text>
          <Text style={styles.text}>Alamat Email: {user.email}</Text>
          <Text style={styles.text}>Nomor Telepon: {user.phone}</Text>
          {user.street && <Text style={styles.text}>Jalan: {user.street}</Text>}
          {user.city && <Text style={styles.text}>Kota: {user.city}</Text>}
          {user.zipcode && (
            <Text style={styles.text}>Kode Pos: {user.zipcode}</Text>
          )}
          {user.companyName && (
            <Text style={styles.text}>Perusahaan: {user.companyName}</Text>
          )}
          <View style={{ borderBottom: '1px dashed #9ca3af', marginTop: 20 }} />
        </View>
      )}
      {user.companyName && (
        <>
          <Text style={styles.branding}>Dibuat oleh: {user.companyName}</Text>
          <Text style={styles.watermark}>{user.companyName}</Text>
        </>
      )}
    </Page>
  </Document>
);

export default function EditUserForm({ user }: { user: User }) {
  const [isPreviewOpen, setIsPreviewOpen] = useState(false);
  const [template, setTemplate] = useState<'simple' | 'detailed'>('simple');

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: user.name,
      email: user.email,
      phone: user.phone,
      companyName: user.company?.name || '',
      street: user.address?.street || '',
      city: user.address?.city || '',
      zipcode: user.address?.zipcode || '',
    },
    mode: 'onChange',
  });

  const formData = form.watch();

  const onSubmit = async (data: FormValues) => {
    const pdfBlob = await pdf(
      <UserProfilePDF user={data} template={template} />
    ).toBlob();
    saveAs(pdfBlob, `${data.name}_profile.pdf`);
  };

  return (
    <>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className='space-y-6'>
          {/* Field Nama */}
          <FormField
            control={form.control}
            name='name'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <UserIcon className='h-5 w-5 text-blue-600' />
                  <span>Nama</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='John Doe'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Email */}
          <FormField
            control={form.control}
            name='email'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <EmailIcon className='h-5 w-5 text-blue-600' />
                  <span>Email</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='john@example.com'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Telepon */}
          <FormField
            control={form.control}
            name='phone'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <PhoneNumberIcon className='h-5 w-5 text-blue-600' />
                  <span>Telepon</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='+6281234567890'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Perusahaan */}
          <FormField
            control={form.control}
            name='companyName'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <CompanyIcon className='h-5 w-5 text-blue-600' />
                  <span>Nama Perusahaan</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='Perusahaan ABC'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Field Alamat */}
          <FormField
            control={form.control}
            name='street'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <AddressIcon className='h-5 w-5 text-blue-600' />
                  <span>Jalan</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='Jalan Contoh 123'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='city'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <CityIcon className='h-5 w-5 text-blue-600' />
                  <span>Kota</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='Jakarta'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name='zipcode'
            render={({ field }) => (
              <FormItem>
                <FormLabel className='text-gray-700 font-semibold flex items-center space-x-2'>
                  <PostalCodeIcon className='h-5 w-5 text-blue-600' />
                  <span>Kode Pos</span>
                </FormLabel>
                <FormControl>
                  <Input
                    {...field}
                    className='border-2 border-gray-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 rounded-xl p-4 transition-all'
                    placeholder='12345'
                  />
                </FormControl>
                <FormMessage className='text-red-500 text-sm' />
              </FormItem>
            )}
          />

          {/* Informasi Tambahan (dipindah ke atas Pilih Template) */}
          <div className='bg-gray-100 p-4 rounded-2xl'>
            <h3 className='text-lg font-semibold text-gray-700 mb-2'>
              Informasi Tambahan
            </h3>
            <p className='text-gray-600 mb-2'>Username: {user.username}</p>
            <p className='text-gray-600 mb-2'>Website: {user.website}</p>
            {user.address?.geo && (
              <p className='text-gray-600 '>
                Koordinat: Lat {user.address.geo.lat}, Lng{' '}
                {user.address.geo.lng}
              </p>
            )}
          </div>

          {/* Bagian Template dan Tombol */}
          <div className='space-y-4 bg-blue-50 p-6 rounded-2xl border border-blue-100'>
            <div>
              <label className='block text-sm font-semibold text-gray-700 mb-3 flex items-center space-x-2'>
                <span>Pilih Template PDF</span>
              </label>
              <select
                value={template}
                onChange={(e) =>
                  setTemplate(e.target.value as 'simple' | 'detailed')
                }
                className='w-full bg-white border-2 border-gray-200 rounded-xl p-3 focus:border-blue-500 focus:ring-2 focus:ring-blue-200 transition-all'
              >
                <option value='simple'>Template Simpel</option>
                <option value='detailed'>Template Detil</option>
              </select>
            </div>
            <div className='flex space-x-3'>
              <Button
                type='button'
                onClick={() => setIsPreviewOpen(true)}
                className='bg-blue-600 hover:bg-blue-700 text-white rounded-xl px-6 py-3 transition-all transform hover:scale-105 flex items-center space-x-2'
              >
                <EyeIcon className='h-5 w-5' />
                <span>Pratinjau PDF</span>
              </Button>
              <Button
                type='submit'
                disabled={!form.formState.isValid}
                className='bg-green-600 hover:bg-green-700 text-white rounded-xl px-6 py-3 transition-all transform hover:scale-105 flex items-center space-x-2 disabled:opacity-70'
              >
                <DownloadIcon className='h-5 w-5' />
                <span>Unduh PDF</span>
              </Button>
            </div>
          </div>
        </form>
      </Form>

      {/* Preview PDF */}
      {isPreviewOpen && (
        <div className='fixed inset-0 bg-black/50 backdrop-blur-sm flex items-center justify-center p-6 z-50 animate-fade-in'>
          <div className='bg-white rounded-2xl w-full max-w-5xl h-[90vh] flex flex-col shadow-2xl overflow-hidden'>
            <div className='p-4 border-b border-gray-200 flex justify-between items-center bg-blue-50'>
              <h3 className='text-lg font-semibold text-blue-600'>
                Pratinjau PDF - {formData.name}
              </h3>
              <Button
                variant='destructive'
                onClick={() => setIsPreviewOpen(false)}
                className='bg-red-600 hover:bg-red-700 rounded-xl px-6 py-2 flex items-center space-x-2'
              >
                <XIcon className='h-5 w-5' />
                <span>Tutup</span>
              </Button>
            </div>
            <div className='flex-1 p-6 bg-gray-50'>
              <PDFViewer
                width='100%'
                height='100%'
                className='w-full h-full rounded-lg shadow-inner border border-gray-200'
              >
                <UserProfilePDF user={formData} template={template} />
              </PDFViewer>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

